<style scoped>
  .delivery-adress{
    
  }
  .delivery-time{

  }
  .center{
    display: flex;
    align-items: center;
    padding: 3px 16px;
    justify-content: left;
    font-size: 14px;
  }
  .center ion-icon{
    margin: 0 3px;
    font-size: 16px;
  }
  .center ion-icon[slot="start"]{
    margin-left: -2px;
  }
  .rightend{
    height: 100%;
    width:90px;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
  }
  .selector{
    display: flex;
    margin: 5px 0;
  }
</style>

<template>
  <ion-list v-if="isMainLocationSet">
      <ion-item lines="none" @click="selectDeliveryAddress()">
        <ion-icon slot="start" :icon="chevronDownOutline"/>
        <ion-text color="medium">Адрес доставки заказа</ion-text>
      </ion-item>
      <ion-item lines="none">
        <ion-icon slot="start" color="primary" :icon="locationOutline"/>
        <ion-text color="dark">{{$heap.state.user?.location_main?.location_address}}</ion-text>
      </ion-item>
  </ion-list>
  <ion-list v-if="showComment && $heap.state.user.location_main">
    <ion-item>
        <ion-textarea rows="1" placeholder="комментарий к адресу" @change="locationCommentChanged()" v-model="$heap.state.user.location_main.location_comment"></ion-textarea>
    </ion-item>
  </ion-list>
</template>

<script>
import { 
  IonIcon,
  IonTextarea,
  IonText,
  IonNote,
  IonLabel,
  IonItem,
  IonList,
 }  from "@ionic/vue";

import { 
  locationOutline, 
  chevronDownOutline 
}                               from "ionicons/icons";
import heap                     from "@/heap";
import router                   from '@/router';
import Topic                    from '@/scripts/Topic.js'
import jQuery                   from 'jquery';

export default {
  props:['deliveryTime','showComment'],
  components: {
      IonIcon,
      IonTextarea,
      IonText,
      IonNote,
      IonLabel,
      IonItem,
      IonList,
  },
  setup() {
    return { 
      locationOutline, 
      chevronDownOutline 
    };
  },
  data() {
    return {
      location_main: heap.state.user.location_main,
    };
  },
  created(){
    let self=this;
    Topic.on('userMainLocationSet',mainloc=>{
      self.location_main=mainloc
    })
    Topic.on('userGet',user=>{
      self.location_main=user.location_main
    })
  },
  methods: {
    selectDeliveryAddress() {
        router.push('/user/user-addresses');
    },
    async locationCommentChanged(){
      const request={
        location_id:heap.state.user.location_main.location_id,
        location_comment:heap.state.user.location_main.location_comment
      };
      jQuery.post( this.$heap.state.hostname + "Location/itemUpdate", JSON.stringify(request) );
    }
  },
  computed: {
    isSignedIn() {
      return heap.state.user.user_id && heap.state.user.user_id > -1;
    },
    isMainLocationSet(){
        return this.location_main?1:0;
    }
  },
};
</script>